.item {
    display: flex;
    flex-direction: column;
    text-align: left;
}

.item img {
    max-width: 100%;
}

.name {
    font-family: 'Margin';
    font-size: 50px;
    margin: 0;
}

.desc {
    font-family: 'Livvic';
    font-size: 30px;
    margin: 0;
}

@media screen and (max-width: 950px) {
    .name {
        font-size: 30px;
    }    

    .desc {
        font-size: 20px;
    }
}

@media screen and (max-width: 375px) {
    .name {
        font-size: 25px;
    }    

    .desc {
        font-size: 16px;
    }
}